@import "https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@100..900&family=Noto+Sans:ital,wght@0,100..900;1,100..900&family=Noto+Serif:ital,wght@0,100..900;1,100..900&display=swap";
* {
  box-sizing: border-box;
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  border: 0;
  margin: 0;
  padding: 0;
}

body {
  background: #fff;
  font-family: Noto Sans TC, sans-serif;
  font-size: 100%;
  line-height: 1.5;
}

h1, h2, h3, h4, h5, h6 {
  font-family: Noto Sans TC, sans-serif;
}

ul, ol {
  list-style: none;
}

img {
  width: 100%;
  display: block;
}

a {
  text-decoration: none;
}

input, select, option {
  outline: none;
}

.question-container {
  justify-content: space-between;
  align-items: center;
  max-width: 900px;
  min-height: 80vh;
  margin: 0 auto;
  padding: 50px 20px;
  display: flex;
}

.question-container .question-left {
  flex: 1;
}

.question-container .question-left h4 {
  color: #3dce94;
  font-size: 1.125rem;
  font-weight: bold;
}

.question-container .question-left h2 {
  color: #2a2324;
  margin-top: 10px;
  font-size: 1.875rem;
}

.question-container .question-left .options {
  justify-content: flex-start;
  margin-top: 30px;
}

.question-container .question-left .options .option {
  cursor: pointer;
  background-color: #d9d9d9;
  border-radius: 25px;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 15px;
  padding: 10px 30px;
  display: flex;
}

.question-container .question-left .options .option:hover {
  background-color: #3dce94;
}

.question-container .question-left .options .option:hover input {
  margin-right: 10px;
}

.question-container .question-left .title {
  display: flex;
}

.question-container .question-left .intro-title {
  color: #3dce94;
  font-size: 2.25rem;
  font-weight: bold;
  display: flex;
}

.question-container .question-left .intro-question {
  color: #424242;
  margin-top: 10px;
  font-size: 1.25rem;
}

.question-container .question-left .form-fields .form-group {
  color: #424242;
  align-items: baseline;
  margin-bottom: 20px;
  padding: 0 2px;
  display: flex;
}

.question-container .question-left .form-fields .form-group label {
  width: 5rem;
  margin-bottom: 5px;
  display: flex;
}

.question-container .question-left .form-fields .form-group .option {
  cursor: pointer;
  background-color: #d9d9d9;
  border-radius: 25px;
  align-items: center;
  padding: 10px 15px;
  display: flex;
}

.question-container .question-left .form-fields .form-group .option:hover {
  background-color: #3dce94;
}

.question-container .question-left .form-fields .form-group .option:hover input {
  margin-right: 10px;
}

.question-container .question-left .form-fields .form-group input, .question-container .question-left .form-fields .form-group select {
  border: 1px solid #aaa6a8;
  border-radius: 5px;
  width: 100%;
  padding: 10px;
}

.question-container .question-left .form-fields .form-group-1 {
  color: #424242;
  align-items: baseline;
  margin-bottom: 20px;
  padding: 0 2px;
  display: flex;
}

.question-container .question-left .form-fields .form-group-1 label {
  width: 5rem;
  margin-bottom: 5px;
  display: flex;
}

.question-container .question-left .form-fields .form-group-1 .option-1 {
  cursor: pointer;
  background-color: #d9d9d9;
  border-radius: 25px;
  align-items: center;
  padding: 10px 15px;
  display: flex;
}

.question-container .question-left .form-fields .form-group-1 .option-1:hover {
  background-color: #3dce94;
}

.question-container .question-left .form-fields .form-group-1 .option-1:hover input {
  margin-right: 10px;
}

.question-container .options.horizontal {
  justify-content: center;
  gap: 10px;
  display: flex;
}

.question-container .option {
  cursor: pointer;
  background-color: #d9d9d9;
  border-radius: 25px;
  justify-content: center;
  align-items: center;
  padding: 10px 20px;
  font-weight: 500;
  transition: background-color .2s;
  display: flex;
}

.question-container .option input[type="radio"] {
  display: none;
}

.question-container .option:hover {
  color: #fff;
  background-color: #6dd6a7;
}

.question-container .option input[type="radio"]:checked + span {
  color: #fff;
  background-color: #3dce94;
  border-radius: 20px;
  padding: 4px 12px;
  font-weight: bold;
}

.question-container .question-right {
  text-align: center;
  flex: 0 0 300px;
}

.question-container .question-right img {
  width: 200px;
  max-width: 100%;
  height: auto;
}

.navigation-buttons {
  text-align: center;
  justify-content: center;
  margin: 30px auto 10px;
  display: flex;
}

.navigation-buttons button {
  color: #fff;
  cursor: pointer;
  background-color: #3dce94;
  border: none;
  border-radius: 10px;
  margin: 0 10px;
  padding: 10px 20px;
  font-size: 16px;
}

.navigation-buttons button:hover {
  background-color: #6dd6a7;
}

.progress-bar {
  background-color: #d9d9d9;
  border-radius: 5px;
  width: 80%;
  height: 8px;
  margin: 0 auto 40px;
  overflow: hidden;
}

.progress-bar .progress {
  background-color: #3dce94;
  width: 0%;
  height: 100%;
  transition: width .3s;
}

.quiz__step {
  display: none;
}

.quiz__step.active {
  display: block;
}

.option input[type="radio"]:checked + span, .option-1 input[type="radio"]:checked + span {
  color: #fff;
  background-color: #3dce94;
}

body {
  background: #fff;
  margin: 0;
  font-family: Noto Sans TC, sans-serif;
}

.shop-banner {
  text-align: left;
  padding: 60px 240px;
}

.shop-banner_content h1, .shop-banner_content h3 {
  margin: 16px auto;
}

.shop-banner_title {
  color: #3dce94;
  font-size: 6rem;
}

.shop-banner_subtitle {
  font-size: 1.5rem;
  font-weight: normal;
}

.shop-button {
  color: #fff;
  cursor: pointer;
  background-color: #3dce94;
  border: none;
  border-radius: 99px;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s;
}

.shop-button:hover {
  background-color: #37b683;
}

.shop-button--rounded {
  border-radius: 50px;
}

.shop-button--outline {
  color: #3dce94;
  background-color: #0000;
  border: 2px solid #3dce94;
}

.shop-product-filter {
  padding: 40px 240px;
}

.shop-product-filter_header {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  display: flex;
}

.shop-product-filter_title {
  margin: 0;
  font-size: 1.5rem;
}

.shop-tag-list {
  flex-wrap: wrap;
  justify-content: flex-end;
  gap: 1rem;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.shop-tag-item {
  cursor: pointer;
  border: 1px solid #aaa6a8;
  border-radius: 30px;
  padding: 6px 16px;
  font-size: .9rem;
  transition: all .3s;
}

.shop-tag-item:hover, .shop-tag-item.is-active {
  color: #fff;
  background-color: #3dce94;
  border-color: #3dce94;
}

.shop_row {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.shop_col {
  flex: 1;
}

.shop_title h2 {
  margin: 0;
}

.shop_tag-list {
  justify-content: flex-end;
  gap: 1rem;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.shop_tag-list li {
  cursor: pointer;
}

.product-carousel {
  align-items: center;
  padding: 40px 240px;
  display: flex;
  position: relative;
}

.product-carousel .carousel-arrow {
  cursor: pointer;
  z-index: 1;
  background-color: #0000;
  border: none;
  padding: 40px 200px;
  font-size: 24px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.product-carousel .carousel-arrow.left {
  left: 0;
}

.product-carousel .carousel-arrow.right {
  right: 0;
}

.product-carousel .carousel-track-wrapper {
  gap: 20px;
  padding: 0 10px;
  display: flex;
  overflow: scroll;
}

.product-carousel .carousel-track {
  flex-wrap: nowrap;
  display: flex;
}

.product-carousel .product-card {
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 12px;
  flex-direction: column;
  flex: 0 0 calc(25% - 15px);
  min-width: 200px;
  max-width: 300px;
  transition: transform .3s;
  display: flex;
  overflow: hidden;
}

.product-carousel .product-image-wrapper {
  aspect-ratio: 1;
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}

.product-carousel .product-image-wrapper .product-image {
  object-fit: cover;
  width: 100%;
  height: 100%;
  transition: opacity .3 ease;
}

.product-carousel .product-image-wrapper:hover .product-image {
  opacity: 0;
}

.product-carousel .product-image-wrapper:hover {
  transition: transform .3 ease;
  background: url("jaso-mock-up_bg_Ca.caf68d7e.svg") center / cover no-repeat;
  transform: scale(1.05);
}

.shop-product-meta {
  justify-content: space-between;
  align-items: center;
  margin: 19px 0 14px;
  display: flex;
}

.shop-product-tags {
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
  gap: 5px;
  display: flex;
}

.shop-product-tags .tag {
  color: #3dce94;
  background-color: #e6fbf3;
  border: 1px solid #3dce94;
  border-radius: 999px;
  padding: 4px 16px;
  font-size: 10px;
  font-weight: 500;
  display: inline-block;
}

.shop-product-icon {
  flex-wrap: wrap;
  justify-content: flex-end;
  align-items: center;
  gap: 3px;
  display: flex;
}

.cart-btn {
  cursor: pointer;
  background: url("shop-btn.04393f8d.svg") center / contain no-repeat;
  border: none;
  width: 30px;
  height: 30px;
  padding: 0;
  transition: all .3s;
}

.cart-btn:hover {
  background-image: url("shop-btn-hover.50fe91f3.svg");
  transform: scale(1.05);
}

.cart-btn.added {
  filter: grayscale();
  opacity: .7;
  pointer-events: none;
}

.like-btn {
  cursor: pointer;
  background: url("like-btn.68f0877d.svg") center / contain no-repeat;
  border: none;
  width: 30px;
  height: 30px;
  padding: 0;
  transition: all .3s;
}

.like-btn.active {
  background-image: url("like-btn-hover.16cbedc9.svg");
  transform: scale(1.05);
}

.like-btn.added {
  filter: grayscale();
  opacity: .7;
  pointer-events: none;
}

.product-title {
  color: #2a2324;
  margin-bottom: 8px;
  font-size: 20px;
  font-weight: 700;
  line-height: 1.4;
}

.product-subtitle {
  color: #2a2324;
  margin-left: 8px;
  font-size: 14px;
  font-weight: 400;
}

.product-desc {
  color: #555;
  -webkit-line-clamp: 3;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
  margin-bottom: 8px;
  font-size: 14px;
  line-height: 1.6;
  display: -webkit-box;
  overflow: hidden;
}

.product-link {
  z-index: 2;
  color: #3dce94;
  align-items: center;
  gap: 4px;
  font-size: 14px;
  font-weight: 600;
  text-decoration: none;
  transition: all .3s;
  display: inline-flex;
  position: relative;
}

.product-link .arrow, .product-link .text {
  opacity: 1;
  transition: transform .4s, opacity .4s;
  display: inline-block;
  transform: translateX(-8px);
}

.product-link:hover .arrow {
  opacity: 2;
  transform: translateX(100px);
}

.product-link:hover .text {
  opacity: 1;
  transform: translateX(-20px);
}

